	module.exports = {"data":{"markdownRemark":{"html":"<p>A game that was conceived as Mr. Higashi when he was a high school student about five years ago from then. Concept is \"toilet tick shooting\". The hero of the anonymous hero scares the scenario of going into the toilet itself to halt the ambition of imamura, stalker outside the space trying to dominate the toilet in the world ...... At the concept stage, full 3D 360 ° STG was planned, but due to technical limitations it started production as 2D vertical STG. While actually dark in the middle of the stage 1 to 2, a part of the barrage pattern of the EX boss was completed. I thought that the concept was 1 to 5 stages, the EXTRA stage was 2, and although the barrage design was also made partly, it was <a href=\"/th06ph\">fascinated by</a> <a href=\"/danmaq_works\">STG</a> and <a href=\"/th06ph\">Kaibaishou PH ~ moonlight</a> <a href=\"/danmaq_works\">that was</a> finally <a href=\"/danmaq_works\">produced under the request at the time</a> It was absorbed and merged around the <a href=\"/th06ph\">girl</a> and the production was interrupted. In creating this game YaneGame SDK was read and understood, which led to a decisive improvement in programming skill and the programming design did not collapse even in big games.</p>\n<ul>\n<li>Genre: Vertical barrage STG</li>\n<li>Language used: Visual C ++. NET 2003 + <a href=\"http://yanesdkdotnet.sourceforge.jp/\">YaneuraoGameSDK</a></li>\n<li>Last Updated: 2004/2</li>\n</ul>","frontmatter":{"cover":null,"date":"2004-01-01T00:00:00+09:00","strDate":"2003/12/31","title":"REVENGER (former name: ost 便)"}}},"pathContext":{"path":"/en/blog/2004/01/01-revenger/","langKey":"en"}}

